import styles from "../Modal.module.css";
import close from "../../../../assets/icons/close.svg";
import deliveryRus from "../../../../assets/icons/delivery-rus.svg";
import {useAppDispatch} from "../../../../redux/hooks";
import {clearModal} from "../../../../redux/modal-reducer";

const DeliveryRusModal = () => {
    const dispatch = useAppDispatch();

    const closeModal = () => {
        dispatch(clearModal());
    }
    return (
        <div className={styles.modalContent}>
            <img onClick={closeModal} className={styles.close} src={close} alt=""/>
            <h3 className={styles.title}>
                  
            </h3>
            <p className={styles.smallText}>
                    ,      , 
                   .     . <br/>
                   ,   :
            </p>
            <ol className={styles.list}>
                <li>   : ,    .</li>
                <li>   :      ,     
                    .
                </li>
            </ol>
            <p className={styles.smallText}>
                 ,    ,         
                 1-3 .            
                .       ,    .
            </p>
            <img className={styles.deliveryRusIcon} src={deliveryRus} alt=""/>
        </div>
    )
}

export default DeliveryRusModal;