import styles from "../Modal.module.css";
import close from "../../../../assets/icons/close.svg";
import phone from "../../../../assets/icons/phone.svg";
import delivery from "../../../../assets/images/delivery.png";
import {useAppDispatch} from "../../../../redux/hooks";
import {clearModal} from "../../../../redux/modal-reducer";

const DeliveryModal = () => {
    const dispatch = useAppDispatch();

    const closeModal = () => {
        dispatch(clearModal());
    }
    return (
        <div className={styles.modalContent}>
            <img onClick={closeModal} className={styles.close} src={close} alt=""/>
            <h3 className={styles.title}>
                  -  . .
            </h3>
            <p className={styles.text}>
                <span className={styles.red}> : </span>   -    30 . .    ,     .
            </p>
            <div className={styles.buttonBlock}>
                <img src={phone} alt=" "/>
                <a href="tel:+79626849473">+79626849473</a>
            </div>
            <p className={styles.text}>
                <a href="mailto:office@petroplast.ru">
                    office@Petroplast.ru
                </a>
            </p>
            <img className={styles.deliveryIcon} src={delivery} alt=""/>
        </div>
    )
}

export default DeliveryModal;